import React, { useRef } from "react";
import TreeView from 'devextreme-react/tree-view'
import FloatingActionButtons from "./Fab.js";
import { familyTree } from "../data/inocentes.js";

const FamilyTreeView = () => {

  const treeRef = useRef(null)

  const newList = familyTree.map(e => {
    const noParent = familyTree.filter(x => x.parentId === e.id)
    if (noParent.length === 0) {
      e.hasItems = false
    }
    return e

  })


  const onItemClick = e => {
    if (e.node.expanded) {
      e.component.collapseItem(e.node.key)
    } else {

      e.component.expandItem(e.node.key)
    }
  }

  const renderTreeViewItem = item => {

    let itemClass = ''

    switch (item.sex) {
      case 'male':
        itemClass = 'male-node'
        break;
      case 'female':
        itemClass = 'female-node'
        break;
      default:
        itemClass = 'clan-node'
        break
    }

    const disputableCase = item.disputableCase ? item.disputableCase : ''

    return (<div className={itemClass}>
      <div className="name">
        {item.name}
        <sup className="fromCivilRecords">{item.fromCivilRecords ? '1' : ''}</sup> <sup>{disputableCase}</sup>
      </div>
      <div>{item.spouse ? <div className={`spouse non-${itemClass}`}>({item.spouse})</div> : ``}</div>
    </div>
    )
  }

  return (
    <div className="form">
      <TreeView id="simple-treeview"
        ref={treeRef}
        onItemClick={onItemClick}
        items={newList}
        virtualModeEnabled={true}
        displayExpr='name'
        dataStructure='plain'
        searchEnabled={true}
        itemRender={renderTreeViewItem}
        rootValue={-1}
      />
      <FloatingActionButtons treeViewRef={treeRef} />
    </div>
  )
}

export default FamilyTreeView
