import React, { useEffect, useState } from "react";
import config from 'devextreme/core/config';
import repaintFloatingActionButton from 'devextreme/ui/speed_dial_action/repaint_floating_action_button';
import { SpeedDialAction } from 'devextreme-react/speed-dial-action';
import { Popup } from "devextreme-react/popup";
const FloatingActionButtons = ({ treeViewRef }) => {

  const [showPopup, setShowPopup] = useState(false)

  const contactDetails = 'sefmangampo@gmail.com'

  useEffect(() => {
    config({
      floatingActionButtonConfig: {
        icon: 'menu',
        shading: true,
        maxSpeedDialActionCount: 4
      },
    });
    repaintFloatingActionButton()

  }, [])

  const treeRefEvent = e => {
    if (e)
      treeViewRef.current._instance.expandAll()
    else
      treeViewRef.current._instance.collapseAll()
  }

  const scrollToTop = e => {
    treeViewRef.current._instance.instance().focus();
  }

  const onHiding = () => {
    setShowPopup(false)
  }

  return (
    <>
      <SpeedDialAction
        key={5}
        icon="info"
        label="Info"
        onClick={() => setShowPopup(true)}
      />
      <SpeedDialAction
        key={1}
        icon="collapse"
        label="Collapse All"
        onClick={() => treeRefEvent(false)}
      />
      <SpeedDialAction
        key={2}
        icon="expand"
        label="Expand All"
        onClick={() => treeRefEvent(true)}
      />
      <SpeedDialAction
        key={3}
        icon="find"
        label="Search"
        onClick={() => scrollToTop()}
      />

      <div className="popup-wrapper">
        <Popup
          key={4}
          visible={showPopup}
          hideOnOutsideClick={true}
          showCloseButton={true}
          title={"Additional Information"}
          onHiding={onHiding}
          container=".dx-viewport"
        >
          <>
            <div className="legend">
              <p>Legend:</p>
              <div className="sup-desc"><sup>1</sup>- based on public records (birth, death, married)</div>
              <div className="sup-desc"><sup>2</sup> - Disputable but most likely correct</div>
              <div className="sup-desc"><sup>3</sup> - Correct clan but disputable position</div>
            </div>

            <div className="foreword">
              This is an attempt to create a collection of family trees of Mangampo family.
              It is highly unlikely that we'll be able to pinpoint the every common anscestor
              as public records available are sparce and human memory is very fragile.<br /> <br /> Moreover, RA 10173 or the
              Data Privacy Act of 2012 has made compilation of records challenging. For this reason,
              entries for persons born 1985 onward are suggested to use their nicknames for
              additional protection from outside prying. <br /><br /> This aims only to be a reference and members
              can and are highly encouraged to check on social media to create bridges to one another.
            </div>

            <div className="contact-details">
              If you have any corrections concerning the records or additional information, please
              feel free to email me at {contactDetails}. It will be highly appreciated. Thank you.
            </div>
          </>
        </Popup>
      </div>


    </>
  )
}

export default FloatingActionButtons
